
#flex-container-top{
    display:flex;
}
#item{
  border: 1px solid black;
  box-shadow:5px 5px gray;
  padding:10px;
  width:85vw;
  margin-left:5vw;
  }
  #item a{
      color:black;
      text-decoration: none;
  }
  #item a:hover{
      background-color:gray;
  }
#error{
    display:grid;
    justify-content: center;
    margin-top:30vh;
}
#display{
    display:grid;
    row-gap: 20px;
    margin-top:50px;
    padding-bottom:20px;
}

.loader {
    margin-top:30vh;
    margin-left:40vw;
    border: 16px solid #f3f3f3;
    border-radius: 50%;
    border-top: 16px solid #3498db;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
  }
  
  /* Safari */
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

  .no-result{
      display:grid;
      justify-content: center;
      margin-top:40vh
  }
  #user{
   position:relative;
   float:right;
   font-size:small;
  }
  #user img{
      width:30px;
      height:30px;
      background-image:url('https://www.gravatar.com/avatar/1e8fea1e41faa03401cd65a0bbb34270?s=256&d=identicon&r=PG&f=1');
      background-size: 30px 30px;

  }
  #user a{
      color:blue;
  }
  #user a:hover{
      background-color: inherit;
      text-decoration: underline;
  }
  #flex-container{
      display:flex;
      font-size: smaller;
      flex-wrap: wrap;
      padding-top:10px;
      width:85vw;
      gap:15px;
  }
  @media screen and (max-width:600px) {
 #display{
     margin-top:10px;
 }      
  }