#flex-container-top{
  display: flex;
  flex-wrap: wrap;
  margin-top:20px;
  margin-left:5vw;
}
.Logo{
  padding-top:10px;
}
#sort_by{
  padding-top:10px;
  margin-left:5vw;
}
.searchForm{
  display:flex;
  flex-direction:row;
  border-radius: 25px;
  padding:10px;
  border:2px solid rgb(41, 42, 43);
  width:40vw;
  position:relative;
  margin-left:5vw;
}

.search.icon {
  color: rgb(41, 42, 43);
  width: 12px;
  height: 12px;
  border: solid 1px currentColor;
  border-radius: 100%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.search.icon:before {
  content: '';
  position: absolute;
  top: 12px;
  left: 5px;
  height: 6px;
  width: 1px;
  background-color: currentColor;
}



#search-bar{
  flex-grow:2;
  border:none;
  float:center;
  background-color: inherit;
  color:black;

}
#search-bar:focus{
  outline:none; 
}

.search-btn{
  padding:3px;
  outline:none;
  background-color: Transparent;
  border:none;
  cursor:pointer;
}

@media screen and (max-width:700px){
  #flex-container-top{
     display:grid;
  }
.Logo{
padding-top: 0;
display:grid;
justify-content: center;
}

#sort_by{
  display:flex;
  justify-content:center;
  margin-left:0;
}
  .searchForm{
    display:flex;
    flex-direction:row;
    border-radius: 25px;
    padding:10px;
    border:2px solid rgb(41, 42, 43);
    width:80vw;
    margin-left:2vw;
    position: relative;
    margin-top:10px;
  }
  #sort{
 padding-left:2px;
  }
}